import { useEffect, useState } from 'react'
import { getDatabase, ref, onValue } from 'firebase/database';
import { app } from "./firebase";

const db = getDatabase(app);

const Orderhistory = () => {
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    // Fetch order history from Firebase Realtime Database
    const ordersRef = ref(db, 'orders');
    onValue(ordersRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        // Convert Firebase object to array
        const orderArray = Object.values(data);
        setOrders(orderArray);
      }
    });

    // Cleanup on unmount
    return () => {
      // Unsubscribe from Firebase Realtime Database
      ordersRef.off('value');
    }
  }, []);

  return (
    <div>
      <h1>Order History</h1>
      <table>
        <thead>
          <tr>
            <th>Order ID</th>
            <th>Item ID</th>
            <th>Price</th>
            <th>Buyer</th>
            <th>Name</th>
            <th>Time</th>
            <th>Customer Name</th>
            <th>Address</th>
            <th>Quantity</th>
          </tr>
        </thead>
        <tbody>
          {orders.map((order) => (
            <tr key={order.itemId}>
              <td>{order.id}</td>
              <td>{order.itemId}</td>
              <td>{order.price}</td>
              <td>{order.buyer}</td>
              <td>{order.name}</td>
              <td>{new Date(order.time).toLocaleString()}</td>
              <td>{order.customerName}</td>
              <td>{order.address}</td>
              <td>{order.quantity}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default Orderhistory;