import { useEffect, useState } from 'react'
import { ethers } from 'ethers'
import Swal from "sweetalert2"
import {getDatabase, push, ref, set} from "firebase/database";
import {app } from "../firebase";
import emailjs from 'emailjs-com';




// Components
import Rating from './Rating'

import close from '../assets/close.svg'
emailjs.init('z8lUixWkV-ioYwQgn');
const db = getDatabase(app);
const Product = ({ item, provider, account, dappazon, togglePop }) => {
  const [order, setOrder] = useState(null)
  const [hasBought, setHasBought] = useState(false)
  const [formData, setFormData] = useState({
    customerName: '',
    customerGmail: '',
    address: '',
    quantity: 1
  });
  const sendConfirmationEmail = () => {
    const emailData = {
      customerName: formData.customerName,
      email: formData.customerGmail, // Assuming the customer's email is the account address
      itemName: item.name,
      itemCost: item.cost,
      address : formData.address,
      quantity : formData.quantity,
      buyer: account,
      time: Date.now(),


    };

    emailjs
    .send('service_76800qp', 'template_e4iavta', emailData)
    .then((response) => {
      console.log('Email sent!', response.status, response.text);
    })
    .catch((error) => {
      console.error('Error sending email:', error);
    });
};
  const fetchDetails = async () => {
    const events = await dappazon.queryFilter("Buy")
    const orders = events.filter(
      (event) => event.args.buyer === account && event.args.itemId.toString() === item.id.toString()
    )

    if (orders.length === 0) return

    const order = await dappazon.orders(account, orders[0].args.orderId)
    setOrder(order)
  }

  const buyHandler = async () => {
    const signer = await provider.getSigner()
    
    
    // Buy item...
    let transaction = await dappazon.connect(signer).buy(item.id, { value: item.cost })
    await transaction.wait()
    
    //
    const dbRef = ref(db, "orders");
  const newOrderRef = push(dbRef);
  set(newOrderRef, {
    itemId: item.id,
    price: item.cost,
    buyer: account,
    name: item.name,
    time: Date.now(),
    customerName: formData.customerName,
    address: formData.address,
    quantity: formData.quantity
  });
  sendConfirmationEmail();
  //send mail data
  
    // Save order in local storage
  // const orders = JSON.parse(localStorage.getItem('orders') || '[]')
  // const order = {
  //   itemId: item.id,
  //   price: item.cost,
  //   buyer: account,
  //   name: item.name,
  //   time: Date.now()
  // }
  // orders.push(order)
  // localStorage.setItem('orders', JSON.stringify(orders))
  // console.log(orders)
  //

    setHasBought(true)
    
    //beatifull alert box
    Swal.fire({
      position: 'center',
      icon: 'success',
      title: 'Order Successfully Placed',
      showConfirmButton: false,
      timer: 1500
    })
  }
  
  const handleFormChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value
    });
  }

  useEffect(() => {
    fetchDetails()
  }, [hasBought])

  return (
    <div className="product">
      <div className="product__details">
        <div className="product__image">
          <img src={item.image} alt="Product" />
        </div>
        <div className="product__overview">
          <h1>{item.name}</h1>

          <Rating value={item.rating} />

          <hr />

          <p>{item.address}</p>

          <h2>{ethers.utils.formatUnits(item.cost.toString(), 'ether')} ETH</h2>

          <hr />

          <h2>Overview</h2>
          <p>
            {item.description}

            Lorem ipsum dolor sit amet consectetur adipisicing elit. Minima rem, iusto,
            consectetur inventore quod soluta quos qui assumenda aperiam, eveniet doloribus
            commodi error modi eaque! Iure repudiandae temporibus ex? Optio!
          </p>
          <button className='product__buy'>
         Add to Cart
      </button>
        </div>

        <div className="product__order">
          <h1>{ethers.utils.formatUnits(item.cost.toString(), 'ether')} ETH</h1>

          <p>
            FREE delivery <br />
            <strong>
              {new Date(Date.now() + 345600000).toLocaleDateString(undefined, { weekday: 'long', month: 'long', day: 'numeric' })}
            </strong>
          </p>

          {item.stock > 0 ? (
            <p>In Stock.</p>
          ) : (
            <p>Out of Stock.</p>
          )}
          <form>
      <label>
        Customer Name:
        <input type="text" name="customerName" value={formData.customerName} onChange={handleFormChange} />
      </label>
      <label>
        Customer mail Address:
        <input type="text" name="customerGmail" value={formData.customerGmail} onChange={handleFormChange} />
      </label>
      <label>
        Address:
        <input type="text" name="address" value={formData.address} onChange={handleFormChange} />
      </label>
      <label>
        Quantity:
        <input type="number" name="quantity" value={formData.quantity} onChange={handleFormChange} />
      </label>
      </form>
      <button className='product__buy' onClick={buyHandler}>
            Buy Now
          </button>
      

          {order && (
            <div className='product__bought'>
              Item bought on <br />
              <strong>
                {new Date(Number(order.time.toString() + '000')).toLocaleDateString(
                  undefined,
                  {
                    weekday: 'long',
                    hour: 'numeric',
                    minute: 'numeric',
                    second: 'numeric'
                  })}
              </strong>
            </div>
          )}
        </div>


        <button onClick={togglePop} className="product__close">
          <img src={close} alt="Close" />
        </button>
      </div>
    </div >
  );
}

export default Product;