import React from 'react'
import "./index.css"

const Contact = () => {
  return (
    <div>
      <h1>Contact Us</h1>
      <p>Have a question or concern? We're here to help! You can contact us using any of the methods below:</p>
      <ul>
        <li>Phone: 1-800-555-1234</li>
        <li>Email: support@shoppingwebsite.com</li>
        <li>Live Chat: Click the "Chat Now" button in the bottom right corner of the screen</li>
      </ul>
      <p>We are available to assist you Monday through Friday, from 9:00am to 5:00pm EST.</p>
    </div>
  )
}

export default Contact;